}
/*
 *图标库
*/
.icon {
  display: block;
  width: 20px;
  height: 20px;
}
.icon a {
  display: block;
  width: 100%;
  height: 100%;
}
.icon.search {
  color: #26272A;
  width: 12px;
  height: 12px;
  margin: 0 20px;
  border: solid 1px currentColor;
  border-radius: 100%;
  transform: rotate(-45deg);
}
.icon.search:before {
  content: "";
  position: absolute;
  top: 12px;
  left: 5px;
  height: 6px;
  width: 1px;
  background-color: currentColor;
}
.icon.back {
  color: #26272A;
  width: 14px;
  height: 14px;
  border-top: solid 2px currentColor;
  border-right: solid 2px currentColor;
  transform: rotate(-135deg);
}
.icon.close {
  position: relative;
  color: #26272A;
}
.icon.close:before {
  content: '';
  position: absolute;
  top: 10px;
  width: 21px;
  height: 1px;
  background-color: currentColor;
  transform: rotate(-45deg);
}
.icon.close:after {
  content: '';
  position: absolute;
  top: 10px;
  width: 21px;
  height: 1px;
  background-color: currentColor;
  transform: rotate(45deg);
}
.icon.previous {
  width: 50px;
  height: 50px;
  background: url(assets/previous.svg);
}
.icon.play {
  width: 60px;
  height: 60px;
  background: url(assets/play.svg);
}
.icon.play.stopPlay {
  background: url(assets/pause.svg);
}
.icon.next {
  width: 50px;
  height: 50px;
  background: url(assets/next.svg);
}
.icon.more {
  position: relative;
  color: #b4b4b4;
  width: 4px;
  height: 4px;
  border-radius: 50%;
  background: currentColor;
  border: solid 1px currentColor;
}
.icon.more:before {
  content: '';
  position: absolute;
  top: -1px;
  left: -10px;
  width: 2px;
  height: 2px;
  border-radius: 50%;
  background: currentColor;
  border: solid 1px currentColor;
}
.icon.more:after {
  content: '';
  position: absolute;
  top: -1px;
  left: 8px;
  width: 2px;
  height: 2px;
  border-radius: 50%;
  background: currentColor;
  border: solid 1px currentColor;
}
